
body {
	margin: 0;
}
.menu {
	background-color: #253A46;
	padding: 20px;
	font-size: 20px;
}
#menu a {
	color: white;
	padding-right: 50px;
}

.tabla {
	font-size: .7rem;
	width: 100%;
	text-align: left;
	background-color: rgb(240, 240, 240);
	border-radius: 1rem ;
}
@media(min-width: 512px){
	.tabla {
		font-size: 1rem;
	} 
}

.tabla td {
	border-top: 1px solid rgb(211, 211, 211);
	padding: 0;
}


tr th{
	padding: 10px;
	text-transform: uppercase;
}

.center {
	text-align: center;
}

.rojo {
	color: red;
}
.centrar{
	display: flex;
	flex-direction: column;
	align-items: center;
}
.pub_container{
	background-color: rgb(243, 243, 243);
	width: 80%;
	padding: 1rem;
	margin-bottom: 1rem;
	border-radius: 2rem;
	box-shadow: -2px 2px 5px rgb(196, 196, 196);
	cursor: pointer;
}
.pub_usuario{
	font-size: 1.6rem;
	margin-bottom: 1rem;
	text-align: center;
	text-transform: capitalize;
}
.pub_titulo {
	font-size: 1.4rem;
}
.comentarios{
	background-color: rgb(255, 255, 255);
	padding: .5rem;
	border-radius: 1rem;
	list-style: none;
}
.comentarios_titulo{
	font-weight: bold;
	color: #253A46;
}
li {
	margin: 15px 0 15px 0;
}
.contenedor_tareas {
	font-size: 20px;
	padding: 1rem;
	border-radius: 1rem;
	background-color: rgb(243, 243, 243);
	box-shadow: -2px 2px 5px rgb(196, 196, 196);
	margin-bottom: 2rem;
}
.check{
	width: 50%;
	margin: .5rem .2rem .5rem 0;
}

.tareas_item{
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: .2rem;
	border-radius: 1rem;
	font-size: .7rem;
}
.tareas_botones{
	width: 9rem;
}
.tareas_botones .btn{
	font-size: .7rem;
}
@media(min-width: 512px){

	.check{
		width: 100%;
		margin: 0;
	}
	.tareas_item{
		font-size: 1rem;
		padding: 0;
	}
	.tareas_botones {
		width: 15em;
	}
	.tareas_botones .btn{
		font-size: 1rem;
		margin-bottom: .5rem;
	}
}

.tareas_item:hover{
	background-color: rgb(235, 235, 235);
}

.guardar{
	width: 50%;
}
.card_container{
	margin-right: 0 auto;
}

@media(min-width: 512px){
	.tarjeta{
		width: 50%;
	}

}
